// Helper function to parse and bold text wrapped with **
const parseBoldText = (text) => {
  const parts = text.split(/(\*\*.*?\*\*)/);
  return parts.map((part, index) =>
    part.startsWith("**") && part.endsWith("**") ? (
      <strong key={index}>{part.slice(2, -2)}</strong>
    ) : (
      part
    )
  );
};

// PricingCard component
const PricingCard = ({
  name,
  price,
  features,
  not_included,
  description,
  old_price,
}) => {
  return (
    <div className=" z-10 font-poppins p-12 md:px-16 flex flex-col max-w-lg text-center text-gray-900 bg-white rounded-3xl border">
      <h3 className="text-start text-xl text-gray-900 mb-4">{name}</h3>

      <div className="flex flex-col text-center justify-center items-baseline mb-12">
        <span className="line-through text-gray-500 text-xl">{old_price}</span>
        <span className="text-4xl font-extrabold font-kantumruy">
          {price}{" "}
          <span className="font-poppins text-gray-500 font-semibold text-xl leading-relaxed">
            TTC
          </span>
        </span>
      </div>

      <ul className="mb-8 space-y-4 text-left">
        {features.map((feature, index) => (
          <li key={index} className="flex items-center space-x-3">
            <Check className="text-blue-500" />
            <span>{parseBoldText(feature)}</span>
          </li>
        ))}
        {not_included.map((item, index) => (
          <li key={index} className="flex items-center space-x-3">
            <Minus className="text-gray-300" />
            <span>{parseBoldText(item)}</span>
          </li>
        ))}
      </ul>
      <a
        href="#contact"
        className="hover:shadow-lg hover:shadow-blue-500/50 duration-300 font-semibold ease-in-out bg-gradient-to-t from-blue-500 via-blue-500 via-60% to-blue-400 text-white bg-primary-600 hover:bg-primary-700 rounded-lg text-sm px-5 py-2.5 text-center"
      >
        Nous contacter
      </a>
    </div>
  );
};

